<template>
  <div class="video-item">
    <div class="video-img" @click="enterListPage(videoData.id,videoData.video)">
      <img class="munt" src="@/img/player.png">
      <img v-if="videoData.video" :src="videoData.video.cover">
    </div>
    <div class="video-button"><img class="ticket" src="@/img/ticket.png">
      <p @click="enterListPage(videoData.id,videoData.video)">查看优惠</p>
    </div>
    <div class="video-des">{{videoData.title}}</div>
  </div>
</template>

<script>
export default {
  name: 'video-item',
  props: {
    video: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  computed: {
    videoData: {
      get () {
        return this.video
      }
    }
  },
  data () {
    return {
    }
  },
  methods: {
    enterListPage (id, video) {
      const { href } = this.$router.resolve({
        name: 'list',
        query: {
          id: id,
          videoData: JSON.stringify(video),
          title: this.videoData.title
        }
      })
      window.open(href, "_blank");
    }
  },
  mounted () {

  }
}
</script>
<style lang="stylus" scoped>
.video-item {
  .video-img {
    width: 95%;
    margin: 0 auto;
    height: 0;
    padding-bottom: 150%;
    overflow: hidden;
    border-radius: 10px;
    position: relative;

    .munt {
      width: 300px;
      height: 300px;
      position: absolute;
      z-index: 100;
      margin-left: 50%;
      margin-top: 75%;
      transform: translate(-50%, -50%);
    }

    img {
      width: 100%;
    }
  }

  .ticket {
    width: 40px;
    height: 30px;
    margin-right: 8px;
    transform: translate(0, -px);
  }

  .video-button {
    display: flex;
    justify-content: center;
    width: 95%;
    margin: 20px auto 0;
    background: red;
    color: #fff;
    line-height: 80px;
    align-items: center;
    border-radius: 5px;
    font-weight: bold;
    font-size: 38px;
  }

  .video-des {
    font-size: 30px;
    font-weight: bold;
    color: #fff;
    width: 90%;
    margin: 10px auto 0;
    word-wrap: break-word;
    word-break: normal;
  }
}
</style>
