<template>
  <scroller :on-refresh="refresh" :on-infinite="infinite" ref="myscroller" class="wrapper">
    <div class="video-wrapper">
      <div class="colums">
        <div class="content" v-for="(item,index) in video" :key="item.id">
          <video-item v-if="index%2===0" :video="item"></video-item>
        </div>
      </div>
      <div class="colums">
        <div class="content" v-for="(item,index) in video" :key="item.id">
          <video-item v-if="index%2===1" :video="item"></video-item>
        </div>
      </div>
    </div>
  </scroller>
</template>

<script>
import videoItem from "./video-item.vue";

export default {
  name: 'video-wrapper',
  components: {
    videoItem
  },
  data () {
    return {
      video: [],
      noData: '',
      page: 0,
      limit: 10
    }
  },
  methods: {
    infinite (done) {
      if (this.noData) {
        setTimeout(() => {
          this.$refs.myscroller.finishInfinite(2);
        })
        return;
      }
      let self = this;//this指向问题
      setTimeout(() => {
        if (!this.getVideoDatasByPage(this.page)) {
          self.noData = "没有更多数据"
        }
        self.$refs.myscroller.resize();
        done()
      }, 1500)
    },

    refresh (done) {
      setTimeout(() => {
        this.top = this.top - 10;
        this.pageInit()
        this.getVideoDatasByPage(0)
        done()
      }, 2000)
    },
    pageInit () {
      this.video = []
      this.page = 0
      this.limit = 10
    },
    async getVideoDatasByPage (page) {
      try {
        let resVideo = await this.$api.WMC.getVideosDatas({
          offset: page,
          limit: this.limit
        })
        if (resVideo.data.code === 0) {
          let arr = [...this.video, ...resVideo.data.data]
          arr = arr.filter((x, index) => {
            var arrids = []
            arr.forEach((item) => {
              arrids.push(item.id)
            })
            return arrids.indexOf(x.id) === index
          })
          this.video = arr
          this.page += this.limit
          return true
        } else {
          return false
        }
      } catch (error) {
        this.$Message.error({
          content: error,
          duration: 4,
          closable: true
        });
      }
    }
  },
  mounted () {
  }
}
</script>

<style lang="stylus" scoped>
.wrapper {
  margin-top: 150px;

  .video-wrapper {
    display: flex;
    margin: 20px 0 160px;
    flex-wrap: wrap;

    .colums {
      display: flex;
      flex-direction: column;
      width: 50%;

      .content {
        .video-item {
          margin-bottom: 20px;
        }
      }
    }
  }
}
</style>
